.base-button {
  min-width: 50px;
  text-align: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border-radius: 4px;
  text-transform: capitalize;
  padding: 10px;
  cursor: pointer;
}
.base-button:hover {
  -webkit-box-shadow: inset -4px -5px 15px 4px rgba(0, 0, 0, 0.071);
          box-shadow: inset -4px -5px 15px 4px rgba(0, 0, 0, 0.071);
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}



